import React from 'react';
// ===================================================================== antd
import { Form, Input, Select, Radio, DatePicker, Modal, Icon, message } from 'antd';
const FormItem = Form.Item;
// ===================================================================== grid 
const leftCol = 'col-md-22';
const rightCol = 'col-md-78';
// ===================================================================== 
class ModalComponent extends React.Component {
	state = {}
	// 
  	handleOk = () => {
  		this.props.form.validateFields((err, values) => {
  			// 
			if (!err) {
				this.setState({ modalLoading : true })
				// 
				if(this.props.isAdd){
					this.props.$http.post('companyNature/addOrUpdate',{...values}).then(data=>{
						this.setState({ modalLoading : false })
						this.props.showModal(false);
						message.success('!', 1, ()=>{
							this.props.refreshFetch(true);
							this.props.form.resetFields();
						});
					},data=>{
						this.setState({ modalLoading : false })
						message.error(data.msg, 1);
					})
				}else{ // 
					this.props.$http.post('companyNature/addOrUpdate',{id:this.props.selectedRows[0].id,...values}).then(data=>{
						this.setState({ modalLoading : false })
						this.props.showModal(false);
						message.success('!', 1, ()=>{
							this.props.refreshFetch();
							this.props.form.resetFields();
						});
					},data=>{
						this.setState({ modalLoading : false })
						message.error(data.msg, 1);
					})
				}
			}
		})
  	}

	render(){
		const { getFieldDecorator } = this.props.form;
		return (
			<Modal
				title={this.props.isAdd ? '' : ''}
	     		visible={this.props.visible}
	      		onOk={this.handleOk}
	      		onCancel={()=>{
	      			this.props.showModal(false);
			  		this.setState({ modalLoading : false })
			  		this.props.form.resetFields();
	      		}}
	      		wrapClassName="vertical-center-modal"
	      		cancelText=""
	      		okText=''
			>
				<Form>
					<ul className="custom-form sub-form res-md-form">
						<li>
							<h6 className={leftCol}></h6>
							<aside className={rightCol}>
								<FormItem>
									{getFieldDecorator('aa',{initialValue:5100})(
										<Input disabled={true} />
									)}
								</FormItem>
							</aside>
						</li>
						<li>
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('a',{ initialValue:this.props.model.a, rules: [{ required: true, message: '' }]})(
										<Input placeholder="" />
									)}
								</FormItem>
							</aside>
						</li>
						<li>
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('b',{ initialValue:this.props.model.b, rules: [{ required: true, message: '' }]})(
										<Input placeholder="" />
									)}
								</FormItem>
							</aside>
						</li>
						<li>
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('dddd',{ initialValue:null, rules: [{ required: true, message: '' }]})(
										<Select placeholder="">
											<Select.Option key="a">a</Select.Option>
										</Select>
									)}
								</FormItem>
							</aside>
						</li>
						<li className="radio-feedback">
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('radioGroup',{initialValue: null,rules: [{ required: true, message: '' }]})(
							            <Radio.Group>
							           		<Radio value={1}></Radio>
							           		<Radio value={0}></Radio>
							            </Radio.Group>
							        )}
								</FormItem>
							</aside>
						</li>
						<li>
							<h6 className={leftCol}></h6>
							<aside className={rightCol}>
								<FormItem>
									{getFieldDecorator('c',{ initialValue:this.props.model.c, rules: [{ required: true, message: '' }]})(
										<Input placeholder="" />
									)}
								</FormItem>
							</aside>
						</li>
						<li>
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('dss',{ initialValue:null, rules: [{ required: true, message: '' }]})(
										<Select placeholder="">
											<Select.Option key="a">a</Select.Option>
										</Select>
									)}
								</FormItem>
							</aside>
						</li>
						<li className="ie-calendar-picker">
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('dssa',{ rules: [{ required: true, message: '' }]})(
										<DatePicker style={{width:'100%'}}
											placeholder=""
											format="YYYY-MM-DD"
											disabledDate={(startValue)=>{
								          		const endValue = this.state.endValue;
	    										if (!startValue || !endValue) { return false; }
	    										return startValue.valueOf() > endValue.valueOf();
								          	}}
											onChange={(value) => {this.setState({startValue:value})}}
								        />
									)}
								</FormItem>
							</aside>
						</li>
						<li className="ie-calendar-picker">
							<h6 className={leftCol}><i>*</i></h6>
							<aside className={rightCol}>
								<FormItem hasFeedback>
									{getFieldDecorator('sss',{ rules: [{ required: true, message: '' }]})(
								        <DatePicker style={{width:'100%'}}
								        	placeholder=""
								        	format="YYYY-MM-DD"
											disabledDate={(endValue) => {
												const startValue = this.state.startValue;
												if (!endValue || !startValue) { return false; }
												return endValue.valueOf() <= startValue.valueOf();
											}}
											onChange={(value) => { this.setState({endValue:value}) }}
								        />
									)}
								</FormItem>
							</aside>
						</li>
					</ul>
				</Form>
				{
					this.state.modalLoading || this.props.modalLoading ? <div className="pop"><Icon type="loading" style={{ fontSize: 24 }} spin /></div> : ''
				}
			</Modal>
		)
	}
}
export default Form.create()(ModalComponent);


